import api from './api';

export const reportTemplateService = {
  // Get all report templates from all endpoints
  getAllTemplates: async () => {
    try {
      // Fetch from all template endpoints
      const [asbestosClearanceTemplates, leadAssessmentTemplates] = await Promise.all([
        api.get('/asbestos-clearance-templates'),
        api.get('/lead-assessment-templates')
      ]);

      // Combine all templates
      const allTemplates = [
        ...asbestosClearanceTemplates.data,
        ...leadAssessmentTemplates.data
      ];

      return allTemplates;
    } catch (error) {
      console.error('Error fetching report templates:', error);
      throw error;
    }
  },

  // Get a specific template by type
  getTemplateByType: async (templateType) => {
    try {
      let response;
      
      if (templateType === 'asbestosClearanceNonFriable' || templateType === 'asbestosClearanceFriable') {
        response = await api.get(`/asbestos-clearance-templates/${templateType}`);
      } else if (templateType === 'leadAssessment') {
        response = await api.get(`/lead-assessment-templates/${templateType}`);
      } else {
        throw new Error(`Unknown template type: ${templateType}`);
      }
      
      return response.data;
    } catch (error) {
      console.error('Error fetching report template:', error);
      throw error;
    }
  },

  // Create a new template
  createTemplate: async (templateData) => {
    try {
      const { templateType } = templateData;
      let response;
      
      if (templateType === 'asbestosClearanceNonFriable' || templateType === 'asbestosClearanceFriable') {
        response = await api.post('/asbestos-clearance-templates', templateData);
      } else if (templateType === 'leadAssessment') {
        response = await api.post('/lead-assessment-templates', templateData);
      } else {
        throw new Error(`Unknown template type: ${templateType}`);
      }
      
      return response.data;
    } catch (error) {
      console.error('Error creating report template:', error);
      throw error;
    }
  },

  // Update an existing template
  updateTemplate: async (templateType, updateData) => {
    try {
      let response;
      
      if (templateType === 'asbestosClearanceNonFriable' || templateType === 'asbestosClearanceFriable') {
        response = await api.put(`/asbestos-clearance-templates/${templateType}`, updateData);
      } else if (templateType === 'leadAssessment') {
        response = await api.put(`/lead-assessment-templates/${templateType}`, updateData);
      } else {
        throw new Error(`Unknown template type: ${templateType}`);
      }
      
      return response.data;
    } catch (error) {
      console.error('Error updating report template:', error);
      throw error;
    }
  },

  // Delete a template
  deleteTemplate: async (templateType) => {
    try {
      let response;
      
      if (templateType === 'asbestosClearanceNonFriable' || templateType === 'asbestosClearanceFriable') {
        response = await api.delete(`/asbestos-clearance-templates/${templateType}`);
      } else if (templateType === 'leadAssessment') {
        response = await api.delete(`/lead-assessment-templates/${templateType}`);
      } else {
        throw new Error(`Unknown template type: ${templateType}`);
      }
      
      return response.data;
    } catch (error) {
      console.error('Error deleting report template:', error);
      throw error;
    }
  },

  // Initialize default templates if they don't exist
  initializeDefaultTemplates: async () => {
    const defaultTemplates = [
      {
        templateType: "asbestosClearanceNonFriable",
        companyDetails: {
          name: "Lancaster & Dickenson Consulting Pty Ltd",
          address: "4/6 Dacre Street, Mitchell ACT 2911",
          email: "enquiries@landd.com.au",
          phone: "(02) 6241 2779",
          website: "www.landd.com.au",
          abn: "74 169 785 915"
        },
        reportHeaders: {
          title: "ASBESTOS REMOVAL CLEARANCE CERTIFICATE",
          subtitle: "Clearance Inspection Report"
        }
      },
      {
        templateType: "asbestosClearanceFriable",
        companyDetails: {
          name: "Lancaster & Dickenson Consulting Pty Ltd",
          address: "4/6 Dacre Street, Mitchell ACT 2911",
          email: "enquiries@landd.com.au",
          phone: "(02) 6241 2779",
          website: "www.landd.com.au",
          abn: "74 169 785 915"
        },
        reportHeaders: {
          title: "ASBESTOS REMOVAL CLEARANCE CERTIFICATE",
          subtitle: "Clearance Inspection Report"
        }
      },
      {
        templateType: "leadAssessment",
        companyDetails: {
          name: "Lancaster & Dickenson Consulting Pty Ltd",
          address: "4/6 Dacre Street, Mitchell ACT 2911",
          email: "enquiries@landd.com.au",
          phone: "(02) 6241 2779",
          website: "www.landd.com.au",
          abn: "74 169 785 915"
        },
        reportHeaders: {
          title: "LEAD ASSESSMENT REPORT",
          subtitle: "Lead Hazard Assessment"
        }
      }
    ];

    try {
      // Check if templates exist and create them if they don't
      for (const template of defaultTemplates) {
        try {
          await reportTemplateService.getTemplateByType(template.templateType);
        } catch (error) {
          if (error.response?.status === 404) {
            // Template doesn't exist, create it
            await reportTemplateService.createTemplate(template);
            console.log(`Created default template for ${template.templateType}`);
          }
        }
      }
    } catch (error) {
      console.error('Error initializing default templates:', error);
      throw error;
    }
  }
};

export default reportTemplateService; 